<template>
    <div class="newcourse">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item>新建课程设计教学</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container">
            <div class="form">
                <el-form ref="form" :model="form" label-width="110px">
                    <el-form-item label="教学名称：">
                        <el-input size="small" v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item  size="small" label="作业提交截止：">
                        <el-col :span="11">
                            <el-date-picker
                                type="date"
                                placeholder="选择日期"
                                v-model="form.date1"
                                value-format="yyyy-MM-dd"
                                style="width: 100%;"
                            ></el-date-picker>
                        </el-col>
                    </el-form-item>
                    <el-form-item label="教师提示：">
                        <el-input  size="small" type="textarea" rows="5" v-model="form.desc"></el-input>
                    </el-form-item>
                    <el-form-item label="学生名单：">
                        <el-table
                            class="stu-list"
                            size="mini"
                            height="330"
                            :data="tableData"
                            style="width: 100%">
                            <el-table-column
                            type="index"
                              label="序号">
                            </el-table-column>
                            <el-table-column
                              label="姓名">
                              <template slot-scope="scope">
                                <span>{{ scope.row.date }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="入学年份">
                              <template slot-scope="scope">
                                <span>{{ scope.row.date }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="学号">
                              <template slot-scope="scope">
                                <span>{{ scope.row.date }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="院系">
                              <template slot-scope="scope">
                                <span>{{ scope.row.date }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="班级">
                              <template slot-scope="scope">
                                <span>{{ scope.row.date }}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                              label="作业项目">
                              <template slot-scope="scope">
                                <el-select size="mini" v-model="value" placeholder="请选择">
                                    <el-option
                                      v-for="item in options"
                                      :key="item.value"
                                      :label="item.label"
                                      :value="item.value">
                                    </el-option>
                                  </el-select>
                              </template>
                            </el-table-column>
                            <el-table-column label="操作" width="80px">
                              <template slot-scope="scope">
                                <el-button
                                  size="mini"
                                  type="danger"
                                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                              </template>
                            </el-table-column>
                          </el-table>
                          <div class="stunumber">
                              <span>共计 23 人</span>
                              <el-button type="primary" size="mini" @click="modifyStuList" plain>修改名单</el-button>
                          </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">创建教学</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog :title="'修改学生名单 （'+form.resource+' )'" :visible.sync="editVisible" width="800px">
            <div class="select-bg">
                <el-select v-model="value" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="value" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
                <el-select v-model="value" size="small" placeholder="请选择">
                    <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                </el-select>
            </div>
            <el-table
                size="mini"
                ref="multipleTable"
                class="editlist"
                :data="tableData"
                tooltip-effect="dark"
                height="300px"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                  type="selection">
                </el-table-column>
                <el-table-column
                  type="index"
                  label="序号">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="姓名">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="入学年份">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="学号">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="院系">
                </el-table-column>
                <el-table-column
                  prop="name"
                  label="班级">
                </el-table-column>
              </el-table>
            <span slot="footer" class="dialog-footer">
                <el-checkbox class="fl" v-model="checked">随机布置作业</el-checkbox>
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button size="small" type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            editVisible:false,
            checked:false,
            tableData: [{
              date: '2016-05-02',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1518 弄'
            }, {
              date: '2016-05-04',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1517 弄'
            }, {
              date: '2016-05-01',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1519 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }, {
              date: '2016-05-03',
              name: '王小虎',
              address: '上海市普陀区金沙江路 1516 弄'
            }],
            options: [{
              value: '选项1',
              label: '黄金糕'
            }, {
              value: '选项2',
              label: '双皮奶'
            }, {
              value: '选项3',
              label: '蚵仔煎'
            }, {
              value: '选项4',
              label: '龙须面'
            }, {
              value: '选项5',
              label: '北京烤鸭'
            }],
            value: '',
            form: {
                name: '',
                region: '',
                date1: '',
                date2: '',
                delivery: true,
                type: ['步步高'],
                resource: '小天才',
                desc: '',
                options: []
            }
        };
    },
    mounted(){
      let {pid} = this.$route.params;
    },
    methods: {
        handleEdit(index, row) {
            console.log(index, row);
        },
        // 删除操作
        handleDelete(index, row) {
            // 二次确认删除
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        },
        modifyStuList(){
            this.editVisible = true;
        }
    }
};
</script>

<style lang='scss'>

</style>

<style lang='scss' scoped>
    .newcourse{
        .crumbs{
          padding-left:20px;
        }
        .stu-list{
            border:1px solid #ccc;
        }
        .stunumber{
            padding:10px 0;
            color:#666;
            span{
                margin-right:40px;
            }
        }
        .editlist{
            margin-top:20px;
            border:1px solid #eee;
        }
    }
</style>


